<template>
  <div>
    <div>
      <nav class="navbar-fixed">
        <div class="bx f">
          <h1>
            <img
              src="../assets/icon_dapp@3x.png"
              v-if="lang=='cn'"
            >
            <img
              src="../assets/icon_dapp@3x.png"
              style="height:30px;"
              v-if="lang=='en'"
            >
          </h1>
          <ul class="f nav hidden-xs-only">
            <li>
              <a
                @click="changeLan('cn')"
                href="JavaScript:;"
                v-if="language == 'en'"
              >
                <img
                  class="lan-img"
                  src=""
                >
              </a>
              <a
                @click="changeLan('en')"
                href="JavaScript:;"
                v-else
              >
                <img
                  class="lan-img"
                  src=""
                >
              </a>
            </li>
            <li>
              <a
                @click="login"
                v-if="!account.name"
                href="JavaScript:;"
              >{{loginLan}}</a>
              <div
                class="account-cell"
                v-else
                href="JavaScript:;"
              >
                <span>{{account.name}}</span>
                <font-awesome-icon
                  class="icon-logout"
                  @click="logout"
                  icon="sign-out-alt"
                />
              </div>
            </li>
          </ul>

          <div class="mobile-nav hidden-sm-and-up">
            <span
              class="login-name"
              v-if="account.name"
            >{{account.name}}</span>
            <span
              class="login-name"
              v-else
              @click="login"
            >{{loginLan}}</span>
            <div class="three col">
              <div
                :class="navbar?'hamburger is-active':'hamburger'"
                @click.stop="menuClick"
              >
                <span class="line"></span>
                <span class="line"></span>
                <span class="line"></span>
              </div>
            </div>
          </div>
        </div>
      </nav>

      <transition name='fade'>
        <div
          class="meun-mobile hidden-sm-and-up"
          v-if="navbar"
        >
          <ul>
            <li>
              <a
                @click="changeLan('cn')"
                href="JavaScript:;"
                v-if="language == 'en'"
              >中文</a>
              <a
                @click="changeLan('en')"
                href="JavaScript:;"
                v-else
              >English</a>
            </li>
            <li>
              <a
                @click="login"
                v-if="!account.name"
                href="JavaScript:;"
              >{{loginLan}}</a>
              <a
                v-else
                @click="logout"
                href="JavaScript:;"
              >{{logoutLan}}</a>
            </li>
          </ul>
        </div>
      </transition>

    </div>
    <div class="exchange-body">
      <div class="btn-row">
        <div
          class="title-btn"
          @click="titleClick(0)"
          :class="{'on':titleBtn===0}"
        >{{body.buy}}</div>
        <div
          class="title-btn"
          @click="titleClick(1)"
          :class="{'on':titleBtn===1}"
        >{{body.sell}}</div>
      </div>
      <div v-if="titleBtn ===0">
        <div class="exchange-box">
          <div class="exchange-title-row">
            <span class="lt exchange-title">{{body.myaddress}}</span>
            <span class="gt">{{account.name}}</span>
          </div>
          <div class="exchange-title">{{body.coinname}}</div>
          <el-select
            class="input"
            v-model="tokenname"
            placeholder="body.choose"
            @change="changeToken"
          >
            <el-option
              v-for="item in tokenarr"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div
          class="exchange-box"
          v-if="addressState === 2"
        >
          <div class="address-intro-font">{{$t('body.buyintro', {coinName:coinname, tokenName: tokenname})}}</div>
          <qrcode-vue
            class="qrcode"
            :value="address"
            :size="150"
            level="H"
          ></qrcode-vue>
          <div class="address-font"><span id="address">{{address}}</span><span
              class="copy-btn"
              @click="copystr(address)"
            >{{body.copy}}</span></div>
        </div>
        <div
          class="exchange-box"
          v-if="addressState === 1"
        >
          <div class="address-waiting">{{body.staterun}}</div>
        </div>
        <div
          class="exchange-box"
          v-if="addressState === 0"
          @click="getaddress()"
        >
          <div class="address-waiting">{{body.getaddress}}</div>
        </div>

        <div class="exchange-box">
          <div class="order-header">{{body.histroytitle}}</div>
          <div class="order-item buy">
            <el-row
              class="order-item-rol"
              v-for="item in coinOrder"
            >
              <el-col
                class="order-name"
                :span="12"
              >{{item.user}}</el-col>
              <el-col
                class="order-status finish"
                :span="12"
              >{{body.statefinish}}</el-col>
              <el-col
                class="order-name"
                :span="12"
              >{{body.money}}:</el-col>
              <el-col
                class="order-price"
                :span="12"
              >+{{item.amount}}</el-col>
              <el-col
                class="order-title"
                :span="6"
              >{{body.id}}:</el-col>
              <el-col
                class="order-address"
                @click.native="copyaddress($event)"
                :span="24"
              >{{item.extern_tx}}</el-col>
            </el-row>
          </div>
        </div>
      </div>
      <div v-if="titleBtn===1">
        <div class="exchange-box">
          <div class="exchange-title-row">
            <span class="lt exchange-title">{{body.payAddress}}</span>
            <span class="gt">{{account.name}}</span>
          </div>
          <div class="exchange-title">{{body.coinname}}</div>
          <el-select
            class="input"
            v-model="coinname"
            placeholder="body.choose"
            @change="coinChange(coinname)"
          >
            <el-option
              v-for="item in coinarr"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
          <div class="exchange-title">{{body.payMoney}}</div>
          <el-input
            class="input"
            type="number"
            v-model="coinnum"
          ></el-input>
          <div class="exchange-title">{{body.myaddress}}</div>
          <el-input
            class="input"
            v-model="coinaddress"
          ></el-input>
          <div
            class="money-row exchange-title"
            @click="sellAll(currentEOS)"
          >
            {{body.mymoney}}：{{currentEOS}}
          </div>
          <el-button
            class="ex-button"
            @click="doAction()"
          >{{body.btn}}</el-button>
        </div>
        <div class="order-box">
          <div class="order-header">
            <div
              class="order-button"
              :class="{'on':orderBtn===0}"
              @click="orderClick(0)"
            >{{body.staterun}}</div>
            <div
              class="order-button"
              :class="{'on':orderBtn===1}"
              @click="orderClick(1)"
            >{{body.statefinish}}</div>
          </div>
          <div
            class="order-item"
            v-if="orderBtn===0"
          >
            <el-row
              class="order-item-rol"
              v-for="item in tokenRun"
            >
              <el-col
                class="order-name"
                :span="12"
              >{{item.from_user}}</el-col>
              <el-col
                class="order-status run"
                :span="12"
              >{{body.staterun}}</el-col>
              <el-col
                class="order-title"
                :span="12"
              >{{body.money}}</el-col>
              <el-col
                class="order-price"
                :span="12"
              >{{item.amount}}</el-col>
              <el-col
                class="order-title"
                :span="6"
              >{{body.myaddress}}:</el-col>
              <el-col
                class="order-address"
                @click.native="copyaddress($event)"
                :span="24"
              >{{item.to_address}}</el-col>
            </el-row>
          </div>
          <div
            class="order-item"
            v-if="orderBtn===1"
          >
            <el-row
              v-for="item in tokenFinish"
              class="order-item-rol"
            >
              <el-col
                class="order-name"
                :span="12"
              >{{item.from_user}}</el-col>
              <el-col
                class="order-status finish"
                :span="12"
              >{{body.statefinish}}</el-col>
              <el-col
                class="order-title"
                :span="12"
              >{{body.money}}</el-col>
              <el-col
                class="order-price"
                :span="12"
              >{{item.amount}}</el-col>
              <el-col
                class="order-title"
                :span="6"
              >{{body.myaddress}}:</el-col>
              <el-col
                class="order-address"
                @click.native="copyaddress($event)"
                :span="24"
              >{{item.to_address}}</el-col>
              <el-col
                class="order-title"
                :span="6"
              >{{body.id}}:</el-col>
              <el-col
                class="order-address"
                @click.native="copyaddress($event)"
                :span="24"
              >{{item.extern_tx}}</el-col>
            </el-row>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Eos from 'eosjs';
import eventHub from '@/utils/event';
import network from '@/utils/network';
import api from '@/utils/eos';
import config from '@/utils/config';
import createHash from 'create-hash';
import QrcodeVue from 'qrcode.vue';
import { clearTimeout } from 'timers';
export default {
  mounted () {
    this.openFullScreen2()
    this.$i18n.locale = this.$lan('lang')
    this.language = this.$lan('lang')
    this.$store.state.lang = this.language
    eventHub.$on('HIDDEN_MENU', () => this.navbar = false);
    this.getCoinOrder()
    if (window.frames.length != parent.frames.length) {
      var interval = setInterval(() => {
        this.login()
        if (this.account.name) {
          clearInterval(interval)
        }
      }, 5000)
    }

    this.$nextTick(() => {
      if (typeof (scatter) !== 'undefined') {
        if (scatter.identity) {
          const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
          this.$store.commit('UPDATE_ACCOUNT', account);
          this.searchAddress()
        }
        if (!this.$store.state.account || !this.$store.state.account.name) {
          this.login()
        }
      } else {
        document.addEventListener('scatterLoaded', () => {
          if (scatter.identity) {
            const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
            this.$store.commit('UPDATE_ACCOUNT', account);
            this.searchAddress()
          }
          if (!this.$store.state.account || !this.$store.state.account.name) {
            this.login()
          }
        })
      }
    })

    var coinname = this.getQueryString('coin')
    if (!coinname) {
      coinname = 'BTC'
    }
    this.coinname = coinname
    this.tokenname = 'E' + coinname
  },

  data () {
    return {
      address: null,
      num: 0,
      titleBtn: 0,
      coinnum: 0,
      orderBtn: 0,
      addressState: 0,
      coinaddress: '',
      coinname: 'BTC',
      coinarr: [{
        value: 'BTC',
        label: 'BTC'
      }, {
        value: 'USDT',
        label: 'USDT'
      }
        // , {
        //     value: 'ETH',
        //     label: 'ETH'
        //   }
      ],
      tokenname: 'EBTC',
      tokenarr: [{
        value: 'EBTC',
        label: 'EBTC'
      }, {
        value: 'EUSD',
        label: 'EUSD'
      }
        // ,{
        //   value: 'EETH',
        //   label: 'EETH'
        // }
      ],
      token: {
        EBTC: 0,
        EETH: 0,
        EUSD: 0,
        EOS: 0
      },
      currentEOS: 0,
      coinOrder: [],
      tokenRun: [],
      tokenFinish: [],
      navbar: false,
      language: 'cn'
    };
  },
  methods: {
    changeLan (lan) {
      this.navbar = false
      this.$i18n.locale = lan
      this.language = lan
      this.$store.state.lang = lan
    },
    menuClick () {
      this.navbar = !this.navbar
    },
    getQueryString (name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return unescape(r[2]);
      }
      return null;
    },
    logout () {
      if (window.frames.length != parent.frames.length) {
        this.navbar = false
        this.$message({
          showClose: true,
          message: this.$t('logout_success'),
          type: 'success',
          duration: 1500
        });
        this.$store.commit('UPDATE_ACCOUNT', {});
      } else {
        this.navbar = false
        scatter.forgetIdentity().then(() => {
          this.$message({
            showClose: true,
            message: this.$t('logout_success'),
            type: 'success',
            duration: 1500
          });
          this.$store.commit('UPDATE_ACCOUNT', {});
        });
      }

    },
    login () {
      if (window.frames.length != parent.frames.length) {
        window.parent.postMessage({
          msgName: "getUserInfo"
        }, '*')
        window.addEventListener("message", event => {
          console.log(event)
          var response = JSON.parse(event.data)
          if (response.msgName === "eventUserInfo") {
            this.$store.commit('UPDATE_ACCOUNT', response.body);
            this.$message({
              showClose: true,
              message: this.$t('login_success'),
              type: 'success',
              duration: 1500
            });
            this.getCoinOrder()
            this.getToken()
            this.searchAddress()
          }
        })
      } else {
        if (scatter) {
          scatter.getIdentity({
            accounts: [network]
          }).then(() => {
            const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
            if (!account) return;
            this.$store.commit('UPDATE_ACCOUNT', account);
            this.$message({
              showClose: true,
              message: this.$t('login_success'),
              type: 'success',
              duration: 1500
            });
            this.getCoinOrder()
            this.getToken()
            this.searchAddress()
          }).catch(e => {
            this.$message({
              showClose: true,
              message: e.message,
              type: 'warning',
              duration: 1500
            });
          });
        }
      }
    },
    getaddressresult () {
      api.getTableRows({
        "scope": this.account.name,
        "code": config.diceAccount,
        "table": "accounts",
        "json": "true"
      }).then((results) => {
        let isExit = false
        for (let i = 0; i < results.rows.length; i++) {
          let coinObj = results.rows[i]
          if (coinObj.symbol == this.tokenname.toUpperCase()) {
            this.address = coinObj.address
            this.addressState = 2
            isExit = true
          }
        }
        if (!isExit) {
          setTimeout(() => {
            this.getaddressresult()
          }, 3000)
        }
      }).catch(e => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    getaddressIframe () {
      window.parent.postMessage({
        msgName: "executeTransaction",
        body: {
          messageId: "WLuSnA5eVb3CXmBV5FkIKLtzkkor6rLl",
          content: {
            account: config.diceAccount,  //合约名
            name: 'newaddress',
            authorization: [{
              actor: this.account.name,
              permission: 'active',
            }],
            data: {
              user: this.account.name,
              symbol: this.tokenname.toUpperCase()
            }
          }
        }
      }, '*')
      window.addEventListener("message", event => {
        var data = JSON.parse(event.data)
        if (data.msgName === "eventTransaction") {
          if (data.code == 200) {
            this.addressState = 1
            this.getaddressresult()
          }
        }
      })
    },
    getaddress () {
      if (window.frames.length != parent.frames.length) {
        this.getaddressIframe()
      } else {
        this.addressState = 1
        const eos = scatter.eos(network, Eos, {});
        eos.transaction(
          {
            actions: [
              {
                account: config.diceAccount,  //合约名
                name: 'newaddress',
                authorization: [{
                  actor: this.account.name,
                  permission: 'active',
                }],
                data: {
                  user: this.account.name,
                  symbol: this.tokenname.toUpperCase()
                }
              }
            ]
          }
        ).then(result => {
          this.addressState = 1
          this.getaddressresult()
        }).catch(e => {
          console.log(e)
          this.addressState = 0
          var errorstr = typeof e === 'string' ? JSON.parse(e) : e
          if (errorstr.error.details[0].message = 'assertion failure with message: already has exchange address') {
            this.getaddressresult()
          }
        })
      }
    },
    getToken () {
      if (!this.account.name) {
        return
      }
      api.getCurrencyBalance({
        json: true,
        code: 'bitpietokens',
        account: this.account.name
      }).then((results) => {
        if (results && results.length != 0) {
          for (var i = 0; i < results.length; i++) {
            var str = results[i].split(" ");
            if (str[1] === 'EBTC') {
              this.token.EBTC = Number(str[0]).toFixed(6)
            } else if (str[1] === 'EETH') {
              this.token.EETH = Number(str[0]).toFixed(4)
            } else if (str[1] === 'EUSD') {
              this.token.EUSD = Number(str[0]).toFixed(2)
            }
          }
          if (this.coinname === 'BTC') {
            this.currentEOS = this.token.EBTC
          } else if (this.coinname === 'ETH') {
            this.currentEOS = this.token.EETH
          } else if (this.coinname === 'USDT') {
            this.currentEOS = this.token.EUSD
          }
        }
      })
    },
    copystr (str) {
      const range = document.createRange();
      range.selectNode(document.getElementById('address'));

      const selection = window.getSelection();
      if (selection.rangeCount > 0) selection.removeAllRanges();
      selection.addRange(range);
      document.execCommand('copy');
      this.$message({
        showClose: true,
        message: this.$t('copy_success'),
        type: 'success',
        duration: 1500
      });
    },
    copyaddress (ev) {
      const range = document.createRange();
      range.selectNode(ev.target);

      const selection = window.getSelection();
      if (selection.rangeCount > 0) selection.removeAllRanges();
      selection.addRange(range);
      document.execCommand('copy');
      this.$message({
        showClose: true,
        message: this.$t('copy_success'),
        type: 'success',
        duration: 1500
      });
    },
    titleClick (num) {
      this.getToken()
      this.titleBtn = num
      if (num === 0) {
        this.searchAddress()
        if (this.account.name) {
          this.getCoinOrder()
        }
      }
      if (num === 1) {
        if (this.orderBtn === 0) {
          this.getTokenRun()
        } else if (this.orderBtn === 1) {
          this.getTokenFinish()
        }
      }
    },
    changeToken () {
      this.address = null
      this.addressState = 1
      if (this.tokenname == 'EBTC') {
        this.coinname = 'BTC'
      } else if (this.tokenname == 'EUSD') {
        this.coinname = 'USDT'
      }
      this.searchAddress()
    },
    doAction () {
      var address = this.coinaddress
      var money = this.coinnum
      var coinname
      if (this.coinname == 'BTC') {
        coinname = 'EBTC'
      } else if (this.coinname == 'USDT') {
        coinname = 'EUSD'
      }
      var valid = WAValidator.validate(address, 'bitcoin');
      if (!valid || address == '') {
        this.$notify.error(this.body.intro1);
        return false
      }
      if (parseFloat(money) > parseFloat(this.currentEOS)) {
        this.$message({
          showClose: true,
          message: this.body.intro2,
          type: 'success',
          duration: 1500
        });
        return
      } else if (parseFloat(money) < 0.001) {
        this.$message({
          showClose: true,
          message: this.$t('body.intro3', { tokenName: coinname }),
          type: 'success',
          duration: 1500
        });
        return
      }
      if (!address) {
        this.$message({
          showClose: true,
          message: this.body.intro4,
          type: 'success',
          duration: 1500
        });
        return
      }
      money = Number(money).toFixed(8)
      if (window.frames.length != parent.frames.length) {
        var data = {
          account: 'bitpietokens',//合约名，
          name: "transfer",//
          authorization: [{
            actor: this.account.name,
            permission: this.account.authority
          }],
          data: {
            from: this.account.name,
            to: config.diceAccount,
            quantity: money + ' ' + coinname,
            memo: address
          }
        }
        window.parent.postMessage({
          msgName: "executeTransaction",
          body: {
            messageId: "WLuSnA5eVb3CXmBV5FkIKLtzkkor6rLl",
            content: {
              account: 'bitpietokens',//合约名，
              name: "transfer",//
              authorization: [{
                actor: this.account.name,
                permission: this.account.authority
              }],
              data: {
                from: this.account.name,
                to: config.diceAccount,
                quantity: money + ' ' + coinname,
                memo: address
              }
            }
          }
        }, '*')
        window.addEventListener("message", event => {
          console.log(event)
          var data = JSON.parse(event.data)
          if (data.msgName == 'eventTransaction' && data.code == 200) {
            this.getToken()
            this.getTokenRun()
            this.$message({
              showClose: true,
              message: this.$t('body.intro5', { coinName: this.coinname }),
              type: 'success',
              duration: 3500
            });
          } else {
            this.$message({
              showClose: true,
              message: 'error',
              type: 'success',
              duration: 3500
            });
          }
        })
      } else {
        const eos = scatter.eos(network, Eos, {});
        eos.transaction({
          actions: [{
            account: 'bitpietokens',//合约名，
            name: "transfer",//
            authorization: [{
              actor: this.account.name,
              permission: this.account.authority
            }],
            data: {
              from: this.account.name,
              to: config.diceAccount,
              quantity: money + ' ' + coinname,
              memo: address
            }
          }]
        }).then((results) => {
          this.getToken()
          this.getTokenRun()
          this.$message({
            showClose: true,
            message: this.$t('body.intro5', { coinName: this.coinname }),
            type: 'success',
            duration: 3500
          });
        }).catch(e => {
          this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
        })
      }

    },
    coinChange () {
      this.coinaddress = ''
      this.coinnum = 0
      this.getToken()
    },
    sellAll (num) {
      this.coinnum = parseFloat(num)
    },
    getCoinOrder () {
      api.getTableRows({
        "scope": config.diceAccount,
        "code": config.diceAccount,
        "table": "buyorders",
        "key_type": "i64",
        "index_position": "2",
        "lower_bound": this.account.name,
        "json": "true"
      }).then((results) => {
        console.log('buyorders results', results)
        if (this.account.name) {
          if (results.rows && results.rows[0]) {
            var data = []
            for (var i = 0; i < results.rows.length; i++) {
              if (results.rows[i].user == this.account.name) {
                data.push(results.rows[i])
              }
            }
            this.coinOrder = data
          }
        } else {
          return false
        }
      }).catch((e) => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    getTokenRun () {
      api.getTableRows({
        "scope": config.diceAccount,
        "code": config.diceAccount,
        "table": "sellorders",
        "json": "true",
        "key_type": "i64",
        "index_position": "2",
        "lower_bound": this.account.name,
      }).then((results) => {
        if (this.account.name) {
          if (results.rows && results.rows[0]) {
            var data = []
            for (var i = 0; i < results.rows.length; i++) {
              if (results.rows[i].from_user == this.account.name) {
                data.push(results.rows[i])
              }
            }
            this.tokenRun = data
          }
        } else {
          return false
        }
      }).catch((e) => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    getTokenFinish () {
      api.getTableRows({
        "scope": config.diceAccount,
        "code": config.diceAccount,
        "table": "soldorders",
        "json": "true",
        "key_type": "i64",
        "index_position": "2",
        "lower_bound": this.account.name,
      }).then((results) => {
        if (this.account.name) {
          if (results.rows && results.rows[0]) {
            var data = []
            for (var i = 0; i < results.rows.length; i++) {
              if (results.rows[i].from_user == this.account.name) {
                data.push(results.rows[i])
              }
            }
            this.tokenFinish = data
          }
        } else {
          return false
        }
      }).catch((e) => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    orderClick (num) {
      this.orderBtn = num
      if (num === 0) {
        this.getTokenRun()
      } else if (num === 1) {
        this.getTokenFinish()
      }
    },
    addressTable () {
      api.getTableRows({
        "scope": config.diceAccount,
        "code": config.diceAccount,
        "table": "newaddresses",
        "json": "true",
        "key_type": "i64",
        "index_position": "2",
        "lower_bound": this.account.name,
        "limit": 1
      }).then((results) => {
        if (results && results.rows[0] && results.rows[0].user && results.rows[0].user == this.account.name) {
          this.addressState = 1
          this.getaddressresult()
        } else {
          this.addressState = 0
        }

      }).catch((e) => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    searchAddress () {
      api.getTableRows({
        "scope": this.account.name,
        "code": config.diceAccount,
        "table": "accounts",
        "json": "true"
      }).then((results) => {
        var a = true
        if (results && results.rows[0] && results.rows[0].address) {
          for (var i = 0; i < results.rows.length; i++) {
            if (results.rows[i].symbol == this.tokenname) {
              this.address = results.rows[i].address
              this.addressState = 2
              a = false
            }
          }
        }
        if (a) {
          this.addressTable()
        }
      }).catch((e) => {
        this.$notify.error(e.message || JSON.parse(e).error.details[0].message);
      })
    },
    openFullScreen2 () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      var intervaltime = setInterval(() => {
        if (window.frames.length != parent.frames.length) {
          if (this.account.name) {
            loading.close();
            clearInterval(intervaltime)
          }
        } else {
          loading.close();
          clearInterval(intervaltime)
        }

      }, 1000);
    }
  },

  watch: {
    account: function () {
      this.getCoinOrder()
    }
  },
  computed: {
    account () {
      return this.$store.state.account;
    },
    body () {
      return this.$t('body')
    },
    account () {
      return this.$store.state.account;
    },
    loginLan () {
      return this.$t('login')
    },
    logoutLan () {
      return this.$t('logout')
    },
    lang () {
      return this.$store.state.lang
    }
  },
  components: {
    QrcodeVue
  }
};
</script>

